@import "../../../variables.scss";

$search-bar-width: 300px;
$search-bar-width-phone: 100px;
$suggestion-padding-width: 10px;

.react-autosuggest__container {
  position: relative;
}

.react-autosuggest__input {
  width: $search-bar-width;
  height: 30px;
  padding: 5px 10px;
  outline: 0;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border: 1px solid $primary-color-dark;
  border-radius: 4px 0px 0px 4px;

  &:hover {
    background-color: $primary-color-light;
  }

  @include phone {
    width: $search-bar-width-phone;
  }
}

.react-autosuggest__input--error {
  color: #c00;
  border-color: #c00;
}

.react-autosuggest__input--focused {
  outline: none;
}

.react-autosuggest__input--open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.react-autosuggest__suggestions-container {
  display: none;
}

.react-autosuggest__suggestions-container--open {
  display: block;
  position: absolute;
  top: 41px;
  width: calc(#{$search-bar-width} + 2 * #{$suggestion-padding-width});
  border: 1px solid $primary-color-dark;
  background-color: #fff;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border-radius: 0px 0px 4px 4px;

  @include phone {
    width: calc(#{$search-bar-width-phone} + 2 * #{$suggestion-padding-width});
  }
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestion {
  color: #000;
  cursor: pointer;
  padding: 10px $suggestion-padding-width;
}

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd;
}
